.menu-bar {
  background-color: #ffffff; /* พื้นหลังสีขาว */
  border-radius: 10px; /* สี่เหลี่ยมมน */
  box-shadow: 0px 4px 4px #00000040; /* เงา */
  height: 650px; /* ความสูง */
  position: fixed; /* เปลี่ยนเป็น fixed แทน absolute */
  top: 64px; /* ระยะห่างจากด้านบน */
  left: 20px; /* ระยะห่างจากซ้าย */
  width: 179px; /* ความกว้าง */
  display: flex; /* ใช้ flexbox */
  flex-direction: column; /* แนวตั้ง */
  align-items: center; /* จัดแนวกลางในแนวนอน */
}

.content {
  background-color: #ffffff; /* พื้นหลังสีขาว */
  border-radius: 20px; /* สี่เหลี่ยมมน 20px */
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.4); /* เงา */
  height: 650px; /* ความสูง */
  position: fixed; /* เปลี่ยนเป็น fixed แทน absolute */
  top: 64px; /* ระยะห่างจากด้านบน */
  left: 220px; /* ระยะห่างจากเมนูบาร์ */
  width: calc(100% - 240px); /* ความกว้าง (รวมระยะห่าง) */
  max-width: 1280px; /* ความกว้างสูงสุด */
  overflow: hidden; /* ซ่อนส่วนเกิน */
}

.nav-link {
  width: 100%;
  flex: 1;
  color: #9d58ba; /* สีข้อความ */
}

.active {
  background-color: #9d58ba !important;/* หรือสีที่คุณต้องการ */
  color: white; /* หรือสีข้อความที่คุณต้องการ */
}

.nav-link:hover {
  background-color: #7b4f8e; /* สีพื้นหลังเมื่อ hover */
  color: white; /* สีข้อความเมื่อ hover */
}

.tab-content {
  padding: 20px; /* เพิ่มพื้นที่รอบๆ เนื้อหา */
}

.nav {
  display: flex; /* ใช้ flexbox */
  flex-direction: column; /* จัดให้เป็นคอลัมน์ */
  align-items: center; /* จัดให้อยู่ตรงกลาง */
  margin-top: 20px; /* ห่างจากด้านบน */
  width: 150px;
}

